.offer {
  padding: 40px 20px 50px 20px;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding: 60px 20px 75px 20px;
  }
}

.offer__container {
  margin: 0 auto 33px;
  max-width: 435px;
}

.offer__header {
  .header-common-mobile();
  margin-bottom: 31px;
}

.offer__description {
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: @main-text-color;
  margin: 0;
}

.offer__button {
  display: block;
  padding-top: 16px;
  padding-bottom: 16px;
  max-width: 458px;
  margin: 0 auto;
}
